import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { DsaService } from 'src/app/service/dsa.service';
import { GlobalService } from 'src/app/service/global.service';
import { SettingService } from 'src/app/service/setting.service';
import Swal from 'sweetalert2';
declare const $: any;

@Component({
    selector: 'app-manage-payout',
    templateUrl: './manage-payout.component.html',
    styleUrls: ['./manage-payout.component.scss']
})
export class ManagePayoutComponent implements OnInit {
    
    isSpinner: boolean = false;
    searchText: any = '';
    managePayout: any = [];
    actionPermissions: any = {};

    filterObj: any = {
        invoice_date_from: '',
        invoice_date_to: '',
        invoice_number: '',
        sortBy: '',
    }

    formObj: any = {
        firstname: '',
        middlename: '',
        lastname: '',
        mobile: '',
        email: '',
    };

    constructor(public gs: GlobalService, 
        private toastr: ToastrService,
        public setService: SettingService,
        public service: DsaService) {
            this.gs.updatePageName('Manage Payout');
         }

    ngOnInit(): void {
        if(this.gs.loginData && this.gs.loginData['_id']){
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                console.log("resp >>", resp);
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
        }
    }

    filterData() {

    }

    // Get Data 
    getData(id: any) {
        this.isSpinner = true;
        this.service.getManagePayout(id).subscribe((resp: any) => {
            if (resp['status']) {
                this.managePayout = resp.data;

            } else {
                this.toastr.error(resp['message'], 'Oops!', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    addNewBuyer() {
        $('#add-new-buyer').modal('show');
    }

}
