import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DsaService } from 'src/app/service/dsa.service';
import { GlobalService } from 'src/app/service/global.service';
import { SettingService } from 'src/app/service/setting.service';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-manage-buyer',
    templateUrl: './manage-buyer.component.html',
    styleUrls: ['./manage-buyer.component.scss']
})
export class ManageBuyerComponent implements OnInit {
    
    isSpinner: boolean = false;
    searchText: any = '';
    manageBuyerList: any = [];
    actionPermissions: any = {};
    userTypeDSA: boolean = true;

    formObj: any = {
        firstname: '',
        middlename: '',
        lastname: '',
        mobile: '',
        email: '',
    };

    constructor(public gs: GlobalService,
        public service: DsaService,
        public router: Router,
        public _route: ActivatedRoute,
        public setService: SettingService,
        public toastr: ToastrService) {
        this.gs.updatePageName('Manage Buyer');
    }

    ngOnInit(): void {
        
        if(this.gs.loginData && this.gs.loginData['_id']){
            this.setService.getPermission({ id: this.gs.loginData['_id'] }).subscribe((resp: any) => {
                this.actionPermissions = resp['data'];
            }, (error: any) => {
                console.log(error);
            });
            
            if(this.gs.loginData['userType'] == 'DSARUSER'){
                this.userTypeDSA = true;
                this._route.queryParamMap.subscribe((params) => {
                    let paramsID = params.get('bcid');
                    if(paramsID != null){
                        this.getAllBuyer(paramsID);
                    } else {
                        this.getAllBuyer(this.gs.loginData['_id']); 
                    }
                });
            } else {
                this.userTypeDSA = false;
                this._route.queryParamMap.subscribe((params) => {
                    let paramsID = params.get('bcid');
                    if(paramsID != null){
                        this.getDscCreatedDSA(paramsID);
                    } else {
                        this.getDscCreatedDSA(this.gs.loginData['_id']); 
                    }
                });
            }
        }
        
    }

    // Get Data 
    getAllBuyer(id: any) {
        this.isSpinner = true;
        this.service.getManageBuyer({id}).subscribe((resp: any) => {
            if (resp['status']) {
                this.manageBuyerList = resp.data;
                
            } else {
                this.toastr.error(resp['message'], 'Oops!', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    // Get DSC Created DSA 
    getDscCreatedDSA(id: any) {
        this.isSpinner = true;
        this.service.getDscCreatedDSA({id}).subscribe((resp: any) => {
            if (resp['status']) {
                this.manageBuyerList = resp.data;
                console.log('this.manageBuyerList', this.manageBuyerList);

            } else {
                this.toastr.error(resp['message'], 'Oops!', {
                    timeOut: 5000,
                    positionClass: 'toast-top-right',
                    progressBar: true,
                    progressAnimation: 'increasing'
                });
            }
            this.isSpinner = false;
        }, (error: any) => {
            this.isSpinner = false;
            console.log(error);
        });
    }

    goToWhishlists(id: any){
        this.router.navigate(['/my-accounts/wish-list'], { queryParams: { bcid: id }, queryParamsHandling: "merge" })
    }

    goToCart(cartId: any){
        this.router.navigate(['/home/cart'], { queryParams: { bcid: cartId }, queryParamsHandling: "merge" })
    }
    
    goToOrderList(id: any){
        this.router.navigate(['/my-accounts/my-order'], { queryParams: { bcid: id } })
    }

    goToEditBuyer(id: any){
        this.router.navigate(['/dsa-buyer-signup'], { queryParams: { pxd: id } })
    }

    goToViewBuyer(id: any){
        this.router.navigate(['/dsa-buyer-signup'], { queryParams: { bvd: id } })
    }

    goToEditDSA(id: any){
        this.router.navigate(['/dsc-create-dsa'], { queryParams: { pxd: id } })
    }

    goToViewDSA(id: any){
        this.router.navigate(['/dsc-create-dsa'], { queryParams: { bvd: id } })
    }

    removeBuyer(id: any){
        Swal.fire({
			title: 'Are you sure?',
			text: 'You will not be able to recover this Buyer!',
			icon: 'warning',
			showCancelButton: true,
			showLoaderOnConfirm: true,
			confirmButtonText: 'Yes, delete it!',
			cancelButtonText: 'No, keep it'
		}).then((result) => {
			if (result.isConfirmed) {
                let data = {
                    id: id,
                    deletedby: this.gs.loginData['_id']
                }
				this.service.deleteBuyer(data).subscribe((resp: any) => {
					if (resp['status']) {
						Swal.fire(
							'Deleted!',
							'Deleted successfully.',
							'success'
						)

						for (let i in this.manageBuyerList) {
							if (this.manageBuyerList[i]._id == id) {
								this.manageBuyerList.splice(i, 1);
							}
						}
					} else {
						this.toastr.error(resp['message'], 'Oops!', {
							timeOut: 5000,
							positionClass: 'toast-top-right',
							progressBar: true,
							progressAnimation: 'increasing'
						});
					}
				});
			} else if (result.dismiss === Swal.DismissReason.cancel) {
				// Swal.fire(
				// 	'Cancelled',
				// 	'Your imaginary file is safe :)',
				// 	'error'
				// )
			}
		});
    }

}
