import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/service/common.service';
import { GlobalService } from 'src/app/service/global.service';

@Component({
    selector: 'app-dsa-account',
    templateUrl: './dsa-account.component.html',
    styleUrls: ['./dsa-account.component.scss']
})
export class DsaAccountComponent implements OnInit {
    constructor(public gs: GlobalService,
        public router: Router,
        public common: CommonService,
        private _route: ActivatedRoute) {
        this._route.queryParamMap.subscribe((params) => {
            this.gs.globalBuyer = params['params']['bcid'] || '';
            if(this.gs.loginData.userType == 'DSARUSER'){
                this.gs.userTypeDSa = this.gs.loginData.userType;
            }
        });
    }

    ngOnInit(): void {
        
    }

}
