import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DsaAccountComponent } from './dsa-account/dsa-account.component';

import { DsaLayoutComponent } from './dsa_layout/dsa_layout.component';
import { ManageBuyerComponent } from './manage-buyer/manage-buyer.component';
import { ManagePayoutComponent } from './manage-payout/manage-payout.component';
import { ManageSalesRespComponent } from './manage-sales-resp/manage-sales-resp.component';

const routes: Routes = [
    {
        path: '', component: DsaLayoutComponent,
        children: [
            {
                path: 'manage-buyers', component: ManageBuyerComponent,
            },
            {
                path: 'manage-sales', component: ManageSalesRespComponent,
            },
            {
                path: 'manage-payout', component: ManagePayoutComponent,
            },
            {
                path: 'dsa-account', component: DsaAccountComponent,
            },
            {
                path: '',
                redirectTo: 'manage-buyers',
                pathMatch: 'full'
            },
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DsaModuleRoutingModule { }
